.intro{
    background-color: white;
    display: flex;

    .left{
        flex: 0.5;
        overflow: hidden;

        .imgContainer{
            width: 700px;
            height: 700px;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            float: center;
            

            img{
                width: 100%;
            }
        }
    }

    .right{
        flex: 0.5;
        position: relative;

        .wrapper{
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            h1{
                font-size: 60px;
                margin: 10px 0;
            }

            h2{
                font-size: 35px;
            }

            h3{
                font-size: 30px;

                span{
                    font-style: 30px;
                }
            }
        }

        a{
            position: absolute;
            bottom: 10px;
            left: 50%;

            img{
                width: 40px;
                animation: arrowBlink 2s infinite;
            }

            @keyframes arrowBlink{
                to{
                    opacity: 0;
                }
            }

            
        }

    }
}